import { Component, OnInit } from '@angular/core';
import { NavController,MenuController } from '@ionic/angular';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-manage-accounts',
  templateUrl: './manage-accounts.page.html',
  styleUrls: ['./manage-accounts.page.scss'],
})
export class ManageAccountsPage implements OnInit {

  constructor(public navCtrl: NavController,
    public modalCtrl: ModalController,
    public menuCtrl : MenuController) { 
 
  }

  ionViewWillEnter() {
    this.menuCtrl.enable(false);
  }

  ngOnInit() {
  }

  goToAccountDetailPage() {
    this.cancelAccountPage();
    this.navCtrl.navigateForward("manage-account-details");
  }

  cancelAccountPage() {
    this.modalCtrl.dismiss({
      'dismissed': true
    });
  }
}
